import { basicInstance } from './index.js';

// ÌöåÏõêÍ∞ÄÏûÖ
function signup(payload, success, fail) {
  basicInstance
    .post(`/api/members`, payload)
    .then(success)
    .catch(fail)
}
// Ïù¥Î©îÏùº Ïù∏Ï¶ù Î≤àÌò∏ Ï†ÑÏÜ°
function sendEmail(payload, success, fail) {
  basicInstance
    .post(`/api/members/auth/email`, payload)
    .then(success)
    .catch(fail)
}
// Ïù¥Î©îÏùº Ïù∏Ï¶ù ÌôïÏù∏
function checkEmail(payload, success, fail) {
  basicInstance
    .post(`/api/members/auth/email/check`, payload)
    .then(success, fail)
    .catch(fail)
}
// Î°úÍ∑∏Ïù∏
function login(payload, success, fail) {
  basicInstance
    .post(`/api/members/login`, payload)
    .then(success)
    .catch(fail)
}
// ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏ Ï†ÑÏÜ°
function sendTemporaryPassword(payload, success, fail) {
  basicInstance
    .post(`/api/members/auth/email/password`, payload)
    .then(success)
    .catch(fail)
}
// ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÌôïÏù∏
function getUserInfo(success, fail) {
  basicInstance
    .get(`/api/members`)
    .then(success)
    .catch(fail)
}
function loginKakao(payload, success, fail) {
  basicInstance
    .post(
      `/api/members/login/kakao`,
      {},
      {
        params: { code: payload }
      }
    )
    .then(success)
    .catch(fail)
}
function loginGoogle(payload, success, fail) {
  basicInstance
    .post(
      `/api/members/login/google`,
      {},
      {
        params: { code: payload }
      }
    )
    .then(success)
    .catch(fail)
}

// ÎßàÏù¥ÌéòÏù¥ÏßÄ ÏàòÏ†ï
function updateUserInfoAPI(payload, success, fail) {
  basicInstance
    .put(`/api/members`, payload)
    .then(success)
    .catch(fail)
}

// ÎπÑÎ∞ÄÎ≤àÌò∏ ÏàòÏ†ï
function updatePassword(payload, success, fail) {
  basicInstance
    .patch(`/api/members/password`, payload)
    .then(success)
    .catch(fail)
}
// ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏
function confirmpassword(payload, success, fail) {
  basicInstance
    .post(`/api/members/password`, payload)
    .then(success)
    .catch(fail)
}
function signOut(success, fail) {
  basicInstance
    .delete(`/api/members`)
    .then(success)
    .catch(fail)
}
export {
  signup,
  sendEmail,
  checkEmail,
  login,
  getUserInfo,
  loginKakao,
  updateUserInfoAPI,
  loginGoogle,
  updatePassword,
  confirmpassword,
  sendTemporaryPassword,
  signOut
}
