import { login, getUserInfo, loginKakao, loginGoogle, updateUserInfoAPI, signOut } from '../../api/user';
import router from '@/router';
import notifications from '@/composables/notifications'

const { notifySuccess, notifyDanger } = notifications();

const state = () => ({
  userInfo: {
  },
});

const getters = {
  getUserInfo: function (state) {
    return state.userInfo
  }
};

const actions = {
  GET_USER_INFO({ commit }) {
    getUserInfo(
      (res) => {
        commit('UPDATE_USER_INFO', res.data.data);
      },
      () => {
        notifyDanger('ÏÑúÎ≤ÑÏóê Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.üò∞')
      }
    )
  },
  LOGIN({ dispatch, commit }, payload) {
    login(
      payload,
      (res) => {
        if (res.data.code === 200) {
          sessionStorage.setItem('accessToken', res.data.data.accessToken)
          commit('SET_USER_ACCESS_INFO', res.data.data)
          dispatch('GET_USER_INFO')
          router.push({ name: 'Main' })
        } else if (res.data.code === 404) {
          notifyDanger('Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî Ïù¥Î©îÏùºÏûÖÎãàÎã§.üò¶')
        }
      },
      () => {
        notifyDanger('Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Í∞Ä ÎßûÏßÄ ÏïäÏäµÎãàÎã§.üòÖ')
      }
    )
  },
  LOGIN_KAKAO({ dispatch, commit }, payload) {
    loginKakao(
      payload,
      (res) => {
        sessionStorage.setItem('accessToken', res.data.data.accessToken)
        commit('SET_USER_ACCESS_INFO', res.data.data)
        dispatch('GET_USER_INFO')
        router.push({ name: 'Main' })
      },
      (err) => {
        console.log(err)
        notifyDanger('ÏÑúÎ≤ÑÏóê Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.üò∞')
      }
    )
  },
  LOGIN_GOOGLE({ dispatch, commit }, payload) {
    loginGoogle(
      payload,
      (res) => {
        sessionStorage.setItem('accessToken', res.data.data.accessToken)
        commit('SET_USER_ACCESS_INFO', res.data.data)
        dispatch('GET_USER_INFO')
        router.push({ name: 'Main' })
      },
      (err) => {
        console.log(err)
        notifyDanger('ÏÑúÎ≤ÑÏóê Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.üò∞')
      }
    )
  },
  updateUserInfo({ commit }, payload) {
    updateUserInfoAPI(
      payload,
      (res) => {
        switch (res.data.code) {
          case 200:
            commit('UPDATE_USER_INFO', res.data.data);
            router.push({ name: 'Main' })
            notifySuccess('ÌöåÏõêÏ†ïÎ≥¥ ÏàòÏ†ï ÏÑ±Í≥µ!üòá')
            break;
          case 400:
            notifyDanger('ÌöåÏõê Ïù∏Ï¶ù Ïã§Ìå®üò∞')
            break;
          case 404:
            notifyDanger('ÌöåÏõêÏ†ïÎ≥¥ ÏàòÏ†ï Ïã§Ìå®üò∞')
        }
      },
      () => {
        notifyDanger('ÏÑúÎ≤ÑÏóê Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.üò∞')
      }
    )
  },
  LOGOUT({ commit }) {
    commit('LOGOUT')
  },
  SIGNOUT({ dispatch }) {
    signOut(
      (res) => {
        if (res.data.code === 200) {
          setTimeout(() => {
            dispatch('LOGOUT')
            router.push({ name: 'Login' })
            notifySuccess('Îñ†ÎÇòÏã†Îã§Îãà ÏïÑÏâΩÏäµÎãàÎã§.ü•∫')
          }, 1200);
        }  else if ( res.data.code === 404) {
        notifyDanger('ÌöåÏõê ÌÉàÌá¥ Ïã§Ìå®.üò∞')
        }
      },
    )
  }
};

const mutations = {
  SET_USER_ACCESS_INFO(state, payload) {
    state.userInfo.path = payload.path
  },
  UPDATE_USER_INFO(state, payload) {
    state.userInfo = {
      ...state.userInfo,
      ...payload
    };
  },
  LOGOUT(state) {
    // state.userInfo.email = ''
    // state.userInfo.nickname = ''
    // state.userInfo.goal = ''
    // state.userInfo.accessToken = ''
    state.userInfo = {}
    sessionStorage.removeItem('accessToken')
    sessionStorage.clear()
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
